.container{
  position: absolute;
  margin-top: -100px;
}
.title {
  font-weight: 800;
  font-size: 120px;
  line-height: 100px;
  position: relative;
  text-align: center;
  letter-spacing: -8px;
  opacity: .9;
}
.subtitle {
  display: block;
  text-align: center;
  text-transform: uppercase;
  padding-top: 50px;
  color: #fff;
  font-size: 17px;
}
@media only screen and (max-width: 600px) {
  .title{
    font-size: 60px;
    line-height: 60px;
  }
  .subtitle{
    font-size: 14px;
  }
}